<template>
  <div>
    <div id="header" class="content">
      <header-nav></header-nav>
    </div>
    <div class="padding-top-100">
      <!-- 表单区 -->
      <div class="content formarea">
        <!-- 头像 -->
        <div class="box2">
          <el-form>
            <el-form-item>
              <el-upload
                class="avatar-uploader ">
                <img v-if="form.img!=null" :src="osshost+form.img+userface" class="avatar">
              </el-upload>
              <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-form-item>
          </el-form>
          <a>
            <el-upload
              action="#"
              :http-request="handleHttpRequest"
              :show-file-list="false"
              class="avatar-uploader ">
              <i class="el-icon-plus step-upicon">更改头像</i>
            </el-upload>
          </a>
        </div>
        <el-form :model="form" label-width="40px">
          <el-form-item label="昵称">
            <el-input v-model="name" maxlength="10" show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="sex">
              <el-radio label="男">男</el-radio>
              <el-radio label="女">女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="生日">
            <el-col :span="11">
              <el-date-picker type="date" placeholder="选择日期" v-model="form.birthday" value-format="yyyy-MM-dd"
                              style="width: 100%;"></el-date-picker>
            </el-col>
          </el-form-item>
          <el-form-item label="地址">
            <el-input v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item label="口味">
            <el-select v-model="form.taste" placeholder="请选择口味">
              <el-option label="酸" value="酸"></el-option>
              <el-option label="甜" value="甜"></el-option>
              <el-option label="苦" value="苦"></el-option>
              <el-option label="辣" value="辣"></el-option>
              <el-option label="咸" value="咸"></el-option>
              <el-option label="麻辣" value="麻辣"></el-option>
              <el-option label="酸甜" value="酸甜"></el-option>
              <el-option label="咸苦" value="咸苦"></el-option>
              <el-option label="酸苦" value="酸苦"></el-option>
              <el-option label="微辣" value="微辣"></el-option>
              <el-option label="变态辣" value="变态辣"></el-option>
              <el-option label="超级咸" value="超级咸"></el-option>
              <el-option label="清淡" value="清淡"></el-option>
              <el-option label="重口味" value="重口味"></el-option>
              <el-option label="无敌酸" value="无敌酸"></el-option>
              <el-option label="腻人甜" value="腻人甜"></el-option>
              <el-option label="通吃" value="通吃"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="选择标签">
            <el-select v-model="form.tag" placeholder="请选标签">
              <el-option label="烘焙大王" value="烘焙大王"></el-option>
              <el-option label="厨娘" value="厨娘"></el-option>
              <el-option label="厨男" value="厨男"></el-option>
              <el-option label="面点王" value="面点王"></el-option>
              <el-option label="宝妈" value="宝妈"></el-option>
              <el-option label="养生达人" value="养生达人"></el-option>
              <el-option label="探店达人" value="探店达人"></el-option>
              <el-option label="瘦身减脂" value="瘦身减脂"></el-option>
              <el-option label="一日三餐" value="一日三餐"></el-option>
              <el-option label="独食者" value="独食者"></el-option>
              <el-option label="爱甜食" value="爱甜食"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="个人简介">
            <el-input
              type="textarea"
              placeholder="请输入个人简介"
              v-model="personinfo"
              rows="6"
              maxlength="120"
              show-word-limit
            >
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button class="orange" @click="pushuserinfo()">保存</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <FooterBottom></FooterBottom>
  </div>
</template>

<script>
  import headerNav from "@/components/headerNav";
  import FooterBottom from "@/components/footer";
  import {editUserInfo} from '@/api/user'
  import {uploadImg} from '../api/operate'
  import {Message} from "element-ui";
  import tools from '@/utils/tools'

  let OSS = require('ali-oss');
  export default {
    components: {
      headerNav,
      FooterBottom
    },
    data() {
      return {
        form: {
          img: null,
          nickname: '',
          birthday: '',
          sex: '',
          taste: '',
          tag: '',
          address: '',
          perinfo: '',
        },
        personinfo:'',
        name:'',
        sex:'',
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
        osshost: process.env.UPLOAD_URL,
      }
    },
    created() {
      this.getUser()
    },
    methods: {
      getUser() {
        this.$store.dispatch('GetUserInfo').then(response => {
          this.form = response.data.result;
          this.name = response.data.result.name;
          this.personinfo = this.form.personinfo;
          if (response.data.result.sex == 1) {
            this.sex = '男'
          } else {
            this.sex = '女'
          }
        }).catch(error => {
        })
      },
      pushuserinfo() {
        this.form.perinfo=this.personinfo;
        this.form.nickname=this.name;
        this.form.sex=this.sex;
        console.log(this.sex)
        editUserInfo(this.form).then(response => {
          let res = response.data;
          if (res.success) {
            Message({
              message: '修改成功',
              type: 'success',
              duration: 5 * 1000
            })
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      handleHttpRequest(a) {//上传封面图
        let dialogImageUrl = a.file.name;
        let type = 3;
        uploadImg(type).then(response => {
          let result = response.data.result;
          let imgUrl;
          imgUrl = result.spaceid + '/' + tools.imgMd5(dialogImageUrl, imgUrl, 'userimg');//文件名 加密
          const client = new OSS({
            region: 'oss-cn-hangzhou',
            accessKeyId: result.AccessKeyId,
            accessKeySecret: result.AccessKeySecret,
            stsToken: result.SecurityToken,
            bucket: 'guanfan'//请求阿里云之后获得
          })
          client.put(imgUrl, a.file).then(results => {
            this.form.img = results.name;
            // if(results.res.requestUrls[0].indexOf('?') != -1){
            //   this.url = results.res.requestUrls[0].split('?')[0];
            // }else{
            //   //console.log('图片100k以内')
            //   this.url = results.res.requestUrls[0]
            // }
            console.log('头像上传成功');
          }).catch((err) => {
            console.log(err);
          });
        }).catch(error => {
          console.log(error)
        })
      },
    }
  }
</script>

<style>
  .formarea {
    transform: translateX(-100px);
    margin-top: 20px;
    width: 600px;
  }
  .box2 {
      position: absolute;
      top: 0px;
      right: -170px;
      height: 175px;
      text-align: center;
  }
  .box2 a {
    display: inline-block;
    text-align: center;
    font-size: 15px;
    color: #FFC700;
    border: 1px solid #FFC700;
    border-radius: 5px;
    padding: 9px 15px;
  }

  .orange {
    background-color: #FFC700;
    color: #fff;
  }

  .el-radio__input.is-checked .el-radio__inner {
    border-color: #FFC700 !important;
    background: #FFC700 !important;
  }
</style>



